import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _this,timer;
export default {
	computed:{
	   i18n() {  
	     return this.$i18nMsg()  
	   }
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetch();
		        }, 1000);
	},
	data() {
		return {
			loadingType:'more',
			pageNum: 1,
			pageSize: 20,
			items:[]
		};
	},
	onNavigationBarButtonTap:function(val){
		 
		this.tapSettingButton() 
	    
	},
	onShow:function(){
		this.fetch()
	},
	onLoad:function(){
		this.fetch()
	},
	methods:{
		pasterNode:function(address){
			uni.setClipboardData({
				data: address
			})
		},
		async fetch(){
			if (this.loadingType==''){
				return
			}
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize
			}
			const res = await this.$http.get('/address/list',{data: data})
			if (res.data.data.isLastPage){
				this.loadingType = ''
			}else {
				this.pageNum +=1
			}
			this.items = res.data.data.list
			console.log(res)
		},
		async delAddress(code){
			let data ={
				addressCode: code
			}
			const res = await this.$http.get('/address/del',{data: data})
			uni.showToast({
				title: '删除成功'
			});
			this.fetch()
			
		},
		tapSettingButton:function(){
			uni.navigateTo({
				url: './createAddress'
			
			});
		}
	}
	
}
